.form__input {
  width: 100%;
  border: 0;
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  padding: 0 0 9px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  height: 17px;
}

.form__input:focus {
  outline: 2px solid #00bfff;
  box-shadow: 0 0 4px 0 #0080aa;
}